.sidebar{
    width: 30%;
    height: 100%;
    align-items: center;
    text-align: center;
    background-color: rgb(75, 73, 73);
    display: flex;
    border: 2px solid black;
    color: white;
    position: fixed;
}

h3{
    font-family: "Times New Roman", Times, serif;
}

img{
    margin:auto;
    width: 30%;
    height: 30%;
    border-radius: 50%;
}

.navlink{
    text-align: center;
    text-decoration: none;
    margin: auto;
    display: inline-block;
    font-size: x-large;
    width: 90%;
    color: white;
    padding-top: 20px;
}