.content{
    width: 70vw;
    height: 100vh;
    margin-left: 30%;
    background: linear-gradient(black, rgb(43, 42, 42));
    color: rgb(248, 163, 153);
    overflow-y: auto;
}

.eduLabel{
    font-size: x-large;
}

.home{
    text-align: left;
   justify-items: center;
    margin: 50px 100px;
    padding: 10px;
    border: 1px solid black;
    background-color: rgb(75, 73, 73);
    color: salmon;
    border-radius: 15px;
}

.link{
    height: 100px;
    width: 100px;
}

@media (max-width: 500px) {
    .home{
        text-align: left;
        justify-items: center;
        padding: 5px;
        border: 1px solid black;
        background-color: rgb(75, 73, 73);
        color: salmon;
        border-radius: 15px;
        width: 90%;
        margin: auto;
    }
    .content{
        width: 70%;
        height: 100%;
        margin-left: 31%;
        margin-top: 5%;
        background: linear-gradient(black, rgb(43, 42, 42));
        color: rgb(248, 163, 153);
        overflow-y: auto;
        position: fixed;
    }
}